<template>
  <div id="app">
    <div v-if="loading" class="loading-overlay">
      <div class="spinner"></div>
      <p class="loading-text">Loading, please wait...</p>
    </div>

    <div v-else>
      <div class="settings">
        <div class="settings-header">
          <div class="title">Random Commanders</div>
          <div class="commander-count-wrapper">
            <label for="commander-count">#</label>
            <input type="number" id="commander-count" v-model.number="commanderCount" min="1" max="10" class="input" />
          </div>
        </div>

        <div class="content" v-show="settingsVisible">
          
          <div class="setting">
            <div class="title">Color Rules</div>              
            <div class="setting-content">
              <button class="btn green small" @click="addRule">Add Rule</button>    
              <div>
                <button class="btn green extra-small random-selector" @click="selectRandomColors">
                  Select Random
                  <div class="embedded">
                    <label for="random-color-count" class="sub-label">#</label>
                    <input v-model.number="randomColorCount" type="number" id="random-color-count" min="1" max="9" class="input" />
                  </div>
                </button>                    
              </div>

              <label class="checkbox"><input type="checkbox" v-model="multicolorOnly" @change="onMulticolorOnlyChange" /> Multicolor Only </label>
              <label class="checkbox"><input type="checkbox" v-model="monocolorOnly" @change="onMonocolorOnlyChange"/> Monocolor Only </label>
            </div>
            
            <div class="color-possibilities">           
              <div class="color-combinations">
                <div class="grid"> 
                  <div v-for="combination in possibleColorCombinations" :key="combination"  class="combination" >
                    <img v-for="(letter, index) in combination.color" :key="index" :class="{ disabled: combination.disabled}" class="color-icon" :src="require(`@/assets/${letter.toUpperCase()}.svg`)" :alt="letter" />                  
                  </div>
                </div>
              </div>
            </div>

            <div v-for="(rule, index) in colorRules" :key="index" class="setting color-rule">
              <div class="rule-header">      
                <div class="title-wrapper">
                  <div class="rule-title">Rule {{ index + 1 }} <span class="remove-rule" @click="removeRule(index)">X</span>
                  
                    <div class="rule-header-colors" :class="{ exactMatch: rule.exactColorMatch }">
                      <img v-if="rule.selectedColors.b" class="color-icon" :class="{ disabled: rule.selectedColorsNotAllowed }" :src="require(`@/assets/B.svg`)" alt="Black" /> 
                      <img v-if="rule.selectedColors.u" class="color-icon" :class="{ disabled: rule.selectedColorsNotAllowed }" :src="require(`@/assets/U.svg`)" alt="Blue" />   
                      <img v-if="rule.selectedColors.g" class="color-icon" :class="{ disabled: rule.selectedColorsNotAllowed }" :src="require(`@/assets/G.svg`)" alt="Green" />  
                      <img v-if="rule.selectedColors.r" class="color-icon" :class="{ disabled: rule.selectedColorsNotAllowed }" :src="require(`@/assets/R.svg`)" alt="Red" />  
                      <img v-if="rule.selectedColors.w" class="color-icon" :class="{ disabled: rule.selectedColorsNotAllowed }" :src="require(`@/assets/W.svg`)" alt="White" />            
                    </div>
                  </div>
                </div>              
              </div>
              <div class="setting-content">
                <div class="colors">
                  <label class="checkbox"><input type="checkbox" v-model="rule.selectedColors.b" /> Black</label>
                  <label class="checkbox"><input type="checkbox" v-model="rule.selectedColors.u" /> Blue</label>
                  <label class="checkbox"><input type="checkbox" v-model="rule.selectedColors.g" /> Green</label>
                  <label class="checkbox"><input type="checkbox" v-model="rule.selectedColors.r" /> Red</label>
                  <label class="checkbox"><input type="checkbox" v-model="rule.selectedColors.w" /> White</label>
                </div>
                <div class="sub-settings">
                  <div class="sub-setting">                 
                      <!-- <label class="checkbox"><input type="checkbox" v-model="rule.selectedColorsRequired" :disabled="!Object.values(rule.selectedColors).some(value => value) || rule.selectedColorsNotAllowed" /> Colors Required</label> -->
                      <label class="checkbox"><input type="checkbox" v-model="rule.exactColorMatch"  /> Exact Match</label>
                      <label class="checkbox"><input type="checkbox" v-model="rule.selectedColorsNotAllowed" :disabled="!Object.values(rule.selectedColors).some(value => value) || rule.selectedColorsRequired" /> Colors Not Allowed</label>
                  </div>               
                </div>
              </div>
            </div>
          </div>

          <div class="setting">
            <div class="title">Rarity</div>
            <div class="setting-content">
              <select v-model="selectedRarity" class="input">
                <option value="any">Any</option>
                <option value="u">Uncommon</option>
                <option value="m">Mythic</option>
              </select>
            </div>
          </div>

          <div class="setting">
            <div class="title">Sets</div> 
            <div class="setting-content">
              <div class="multiselect-wrapper">
                <multiselect
                  v-model="selectedSets"
                  :options="sets"
                  :searchable="true"
                  :close-on-select="true"
                  :clear-on-select="true"
                  :multiple="true" 
                  label="name"
                  track-by="id"
                  placeholder="Select sets"
                />
              </div>

              <div class="sub-settings">
                <div class="sub-setting"> 
                  <div>
                    <button class="btn green extra-small random-selector" @click="selectRandomSets">
                      Select Random
                      <div class="embedded">
                        <label for="random-set-count" class="sub-label">#</label>
                        <input v-model.number="randomSetCount" type="number" id="random-set-count" min="1" max="9" class="input" />
                      </div>
                    </button>                    
                  </div> 
                </div>              
              </div>
              
            </div>
          </div>   

          <div class="setting">
            <div class="title">Creature Types</div>
            <div class="setting-content">   
              <div class="multiselect-wrapper">
                <multiselect
                  v-model="selectedCreatureTypes"
                  :options="creatureTypes"
                  :searchable="true"
                  :close-on-select="true"
                  :clear-on-select="true"
                  :multiple="true" 
                  placeholder="Select creature types"
                />
              </div>

              <div class="sub-settings">
                <div class="sub-setting">                   
                  <div>
                    <button class="btn green extra-small random-selector" @click="selectRandomCreatureTypes">
                      Select Random
                      <div class="embedded">
                        <label for="random-creature-count" class="sub-label">#</label>
                        <input v-model.number="randomCreatureCount" type="number" id="random-creature-count" min="1" max="9" class="input" />
                      </div>
                    </button>                    
                  </div>

                  <label class="checkbox">
                    <input type="checkbox" v-model="creatureTypesNotAllowed" />
                    Types not allowed
                  </label>   
                </div>              
              </div> 
            </div>                 
          </div>            

          <div class="setting">
            <div class="title">Restriction</div>
            <div class="setting-content">
              <select v-model="restriction" class="input">
                <option value="none">None</option>
                <option value="partners">Partners Only</option>
                <option value="backgrounds">Choose a Background Only</option>
              </select>
            </div>
          </div>

        </div>

        <div class="settings-footer">
          <button @click="fetchCommanders" class="btn blue">Get Commanders</button>          
          <div >
            <a :href="getScryfallLink()" target="_blank" class="btn green">Scryfall Search</a>

            <button @click="toggleSettings" class="btn toggle">
              <span v-if="settingsVisible">▲ Hide Settings</span>
              <span v-else>▼ Show Settings</span>
            </button>
          </div>
        </div>
      </div>

      <div class="messages" v-if="messages.length > 0">
        <div v-for="message in messages" :key="message" class="message">
          {{message}}         
        </div>
      </div>

      <div class="commanders" v-if="commanders.length > 0">
        <div v-for="commander in commanders" :key="commander.id" class="commander-card">          

          <div class="commander-img-wrapper">
            <a :href="commander.scryfall_uri" target="_blank" class="commander-link">
              <img :src="getImageUrl(commander)" alt="commander image" class="commander-img" crossorigin="anonymous">
            </a>
          </div>

          <div class="commander-info">            
            <h2>{{ commander.name }}</h2>
            <div class="commander-info-prices">
              <div v-if="commander.prices.usd" class="commander-info-price">
                ${{ commander.prices.usd }}
              </div>
              <div v-if="commander.prices.usd_foil" class="commander-info-price foil">
                *${{ commander.prices.usd_foil }}
              </div>
              <div v-if="commander.prices.usd_etched" class="commander-info-price etched">
                **${{ commander.prices.usd_etched }}
              </div>
            </div>            
            
            <button v-if="commander.card_faces && commander.card_faces.length > 1 && commander.card_faces[0].image_uris" 
                    @click="toggleFace(commander)" 
                    class="btn green small">
              Transform
            </button>
          </div>
        </div>
      </div>

      <div v-if="commanders.length > 0" class="generate-image-button-container">
        <button @click="generateImage" class="btn green">
          Generate Image of Commanders
        </button>
      </div>       
    </div>

    <canvas ref="canvas" style="display: none;"></canvas>
  </div>
</template>

<script>
  import Multiselect from 'vue-multiselect';
  import setsData from '@/assets/sets.json';
  import creatureTypes from '@/assets/creature-types.json';
  const mtgColors = ["w","u","b","r","g","wu","ub","br","rg","gw","wb","ur","bg","rw","gu","wub","ubr","brg","rgw","gwu","wbr","urg","bgw","rwu","gub","wubr","ubrg","brgw","rgwu","gwub","wubrg"];
      
export default {
  components: {
    Multiselect,  
  },
  data() {
    return {
      devMode: true,
      commanders: [],
      loading: false,
      commanderCount: 5,
      selectedColors: { u: false, b: false, w: false, r: false, g: false, },
      multicolorOnly: false,
      monocolorOnly: false,
      colorCountLimit: 0,
      randomColorCount: 3,
      colorCombination: "any",
      restriction: "none",
      selectedRarity: "any",
      lastApiCallTime: 0,
      settingsVisible: false,
      sets: setsData.data.filter(set => !set.parent_set_code),
      selectedSets: [],
      randomSetCount: 3,
      creatureTypes: creatureTypes.data,
      selectedCreatureTypes: [], 
      pickRandomCreatureTypes: false,
      randomCreatureCount: 3, 
      creatureTypesNotAllowed: false,
      messages: [],
      colorRules: []
    };
  },
  computed: {
    possibleColorCombinations() {
      const possibles = Object.entries(mtgColors).map(([index,key]) => ({ color:key, disabled: true })); 

      const colorRules = this.colorRules
          .filter(rule => Object.entries(rule.selectedColors).some(([key, value]) => value))
          .map(rule => ({
            rule: rule,
            colors: Object.entries(rule.selectedColors).filter(([_, isSelected]) => isSelected).map(([color]) => color)
          }));

      let colors = colorRules.filter(color => !color.rule.selectedColorsRequired && !color.rule.selectedColorsNotAllowed); 
      if(!colors || colors.length == 0) {
        for(const c of ["w","u","b","r","g"]) {
          colors.push({colors: [c], rule: {exactColorMatch: false}});
        }
      }

      for (const c of colors) {
        possibles.filter((p) => (!c.rule.exactColorMatch || c.colors.length == p.color.length) && c.colors.every((c) => p.color.includes(c))).forEach((p) => p.disabled = false);
      }
     
      const requiredColors = colorRules.filter(color => color.rule.selectedColorsRequired);
      for (const rc of requiredColors) {
          possibles.filter((p) => !rc.colors.every((c) => p.color.includes(c))).forEach((p) => p.disabled = true);
      }
      
      const disallowedColors = colorRules.filter(color => color.rule.selectedColorsNotAllowed);    

      for (const dc of disallowedColors) {
          possibles.filter((p) => dc.colors.every((c) => p.color.includes(c))).forEach((p) => p.disabled = true);
      }
     
      if(this.multicolorOnly)
        possibles.filter((p) => p.color.length == 1).forEach((p) => p.disabled = true);
      
      if(this.monocolorOnly)
        possibles.filter((p) => p.color.length > 1).forEach((p) => p.disabled = true);

      return possibles;
    }
  },
  methods: { 
    addRule() {
      this.colorRules.push({
        selectedColors: { u: false, b: false, w: false, r: false, g: false },
        selectedColorsNotAllowed: false,
        selectedColorsRequired: false,
        exactColorMatch: false
      });
    },
    removeRule(index) {
      this.colorRules.splice(index, 1);
    },

    toggleSettings() {
      this.settingsVisible = !this.settingsVisible;
    },
    toggleFace(commander) {
      commander.isTransformed = !commander.isTransformed;
    },
    onMulticolorOnlyChange(event) {
      if(event.target.checked)
        this.monocolorOnly = false;
    },
    onMonocolorOnlyChange(event) {
      if(event.target.checked)
        this.multicolorOnly = false;
    },
    selectRandomFrom(availableItems, limit) {
      const count = Math.min(limit, availableItems.length);
      for (let i = availableItems.length - 1; i > 0; i--) {
        const randomIndex = Math.floor(Math.random() * (i + 1));
        [availableItems[i], availableItems[randomIndex]] = [availableItems[randomIndex], availableItems[i]];
      }
      return availableItems.slice(0, count);
    },
    selectRandomColors() {
      if(this.colorRules.length > 0) {
        this.colorRules.splice(0);
      }

      const possibles = Object.entries(mtgColors)
        .filter((color) => this.multicolorOnly ? color.length > 1 : (this.monocolorOnly  ? color.length == 1 : true))
        .map(([_, color]) => color);
      const randomSelections = this.selectRandomFrom(possibles, this.randomColorCount);

      for (const rs of randomSelections) {
        const rule = {
          selectedColors: { u: false, b: false, w: false, r: false, g: false },
          selectedColorsNotAllowed: false,
          selectedColorsRequired: false,
          exactColorMatch: true
        };

        for (const c of rs.split("")) {
          rule.selectedColors[c] = true;
        }
        
        this.colorRules.push(rule);
      }
    },
    selectRandomSets() {
      this.selectedSets = this.selectRandomFrom([...this.sets], this.randomSetCount);
    },
    selectRandomCreatureTypes() {      
      this.selectedCreatureTypes = this.selectRandomFrom([...this.creatureTypes], this.randomCreatureCount);
    },

    getScryfallLink() {
      const queryFilter = this.getQueryFilter();
      const searchQuery = this.combineFilters("is:commander", queryFilter.full, "game:paper", "f:commander");      
      return `https://scryfall.com/search?q=${searchQuery}`;
    },
    
    getQueryFilter() {
        const colorRules = this.colorRules
          .filter(rule => Object.entries(rule.selectedColors).some(([key, value]) => value))
          .map(rule => ({
            rule: rule,
            colors: Object.entries(rule.selectedColors).filter(([_, isSelected]) => isSelected).map(([color]) => color)
          }));

        const requiredColors = colorRules.filter(color => color.rule.selectedColorsRequired);        
        const disallowedColors = colorRules.filter(color => color.rule.selectedColorsNotAllowed);
        const colors = colorRules.filter(color => !color.rule.selectedColorsRequired && !color.rule.selectedColorsNotAllowed);

        const colorsFilter = colors?.length > 0
          ? `(${colors.map((color) => `id${color.rule.exactColorMatch ? "=" : ">="}${color.colors.join("")}`).join("%20OR%20")})`//`id>=${colors.map((color) => color).join("")}`
          : ``;

        const requiredColorsFilter = requiredColors?.length > 0
          ? requiredColors.map((color) => `id${color.rule.exactColorMatch ? "=" : ">="}${color.colors.join("")}`).join("%20") //`id>=${requiredColors.join("")}` 
          : ``;
        
        const disallowedColorsFilter = disallowedColors?.length > 0
          ? disallowedColors.map((color) => `-id${color.rule.exactColorMatch ? "=" : ">="}${color.colors.join("")}`).join("%20") //`-id>=${disallowedColors.join("")}` 
          : ``;

        const colorCombinationFilter = this.monocolorOnly 
          ? "id=1"
          : (this.multicolorOnly && colors.length != 1 ? "id>1" : "");
        const colorFilter = colorsFilter || requiredColorsFilter || disallowedColorsFilter
          ? `(${this.combineFilters(colorsFilter, requiredColorsFilter, disallowedColorsFilter, "-id=c", colorCombinationFilter)})`
          : colorCombinationFilter;

        const rarityFilter = this.selectedRarity !== 'any' ?`rarity:${this.selectedRarity}` : "";
        
        const restrictionFilter = this.restriction == "backgrounds"
          ? `o:"Choose a background"`
          : (this.restriction == "partners" ? `o:/^partner$/` : "");

        const sets = this.selectedSets.map((set) => `set:${set.code}`).join("%20OR%20");
        const setFilter = sets ? `(${sets})` : "";

        const creatureTypeNotAllowed = this.creatureTypesNotAllowed ? "-" : ""; 
        const creatureTypesSeperator = this.creatureTypesNotAllowed ? "%20" : "%20OR%20";
        const creatureTypes = this.selectedCreatureTypes.map((creatureType) => `${creatureTypeNotAllowed}t:${creatureType}`)
          .join(creatureTypesSeperator);
        const creatureTypeFilter = creatureTypes ? `(${creatureTypes})` : "";

        const fullFilter = this.combineFilters(colorFilter, rarityFilter, restrictionFilter, setFilter, creatureTypeFilter);

        return {
          color: colorFilter,
          rarity: rarityFilter,
          set: setFilter,
          creatureTypeFilter: creatureTypeFilter,
          full: fullFilter
        };
    },
    buildQuery(commanders, queryFilter) {
      if(commanders.length == 0)
        return queryFilter;
      return this.combineFilters(queryFilter, `(${commanders.map(c => `-"${c.name}"`).join("%20")})`);
    },

    async fetchCommanders() {
      
      this.loading = true;
      this.settingsVisible = false;
      this.messages = [];

      try {
        const commanders = [];
        const queryFilter = this.getQueryFilter();

        for (let i = 0; i < this.commanderCount; i++) {
          const filter = this.buildQuery(commanders, queryFilter.full);
          const commander = await this.fetchRandomCommander(filter);
          if(commander == null)
            break;
          commanders.push(commander);
        }
        
        this.commanders = await this.includeAdditionalCommanders(commanders, queryFilter);
        if(this.commanders.length === 0) {
          this.messages.push("No commanders found with the selected critera");
        } 
        this.loading = false;
      } catch (error) {
        this.commanders = [];
        console.error("Error fetching commanders:", error);
        this.messages.push(`Error fetching commanders: ${error}`);
        this.loading = false;
      }
    },

    async includeAdditionalCommanders(commanders, queryFilter) {
      const that = this;
      const named = {
        Partner: {
          fetchFilter: "o:/^partner$/",
          count: this.countMatchingCommanders(commanders, "Partner", "Partner with"),
          shouldFetch() {
            return this.count == 1;
          }
        },
        FriendsForever: {
          fetchFilter: "o:/^friends forever$/",
          count: this.countMatchingCommanders(commanders, "Friends forever"),
          shouldFetch() {
            return this.count == 1;
          }
        },
        Doctor: {          
          fetchFilter: "o:/^doctor's companion$/",
          count: this.countMatchingCommanders(commanders, null, null, "Legendary Creature — Time Lord Doctor"),
          shouldFetch() {
            return this.count > 0 && named.DoctorsCompanion.count == 0;
          }
        },
        DoctorsCompanion : {
          fetchFilter: 't:"Time Lord Doctor"',
          count: this.countMatchingCommanders(commanders, "Doctor's companion"),
          shouldFetch() {
            return this.count > 0 && named.Doctor.count == 0;
          }
        },
        Backgrounds: {
          fetchFilter: "t:background",
          count: this.countMatchingCommanders(commanders, "Choose a background"),
          shouldFetch() {
            return this.amountToFetch() > 0;
          },
          amountToFetch() {
            return that.restriction == "backgrounds" ? commanders.length : (this.count > 0 ? 1 : 0);
          }
        }
      };

      for (const [key, value] of Object.entries(named)) {
        if(value.shouldFetch()) {
          const amountToFetch = 'amountToFetch' in value ? value.amountToFetch() : 1;
          const filter = `${value.fetchFilter}${queryFilter.rarity}`;
          for(let i = 0; i < amountToFetch; i++) {
            const commander = await this.fetchRandom(this.buildQuery(commanders, filter));
            if(commander == null)
              break;
            
            commanders.push(commander);
          }          
        }
      }
      return commanders;
    },

    countMatchingCommanders(commanders, includeKeyword = null, excludeKeyword = null, typeLine = null) {
      return commanders.filter(
        (cmd) => (!includeKeyword || cmd.keywords?.includes(includeKeyword))
          && (!excludeKeyword || !cmd.keywords?.includes(excludeKeyword))
          && (!typeLine || cmd.type_line == typeLine)
      ).length;
    },

    async fetchUnique(commanders, fetchFunction, queryFilter) {
      const maxRetries = 4;
      let retries = 0;

      do {
        const commander = await fetchFunction(queryFilter);
        if (!commanders.some((c) => c.id === commander.id)) return commander;
        retries++;
      } while (retries < maxRetries);

      return null;
    },

    async ensureDelay(ms) {
      const currentTime = Date.now();
      const timeSinceLastCall = currentTime - this.lastApiCallTime;
      const remainingTime = Math.max(0, ms - timeSinceLastCall);
      if (remainingTime > 0) {
        await new Promise(resolve => setTimeout(resolve, remainingTime)); 
      }
      this.lastApiCallTime = Date.now();
    },       
    async fetchRandom(queryFilter) {
      await this.ensureDelay(100);
      const response = await fetch(`https://api.scryfall.com/cards/random?q=${this.combineFilters(queryFilter, "game:paper", "f:commander")}`);
      const data = await response.json();
      return data?.object == "error" ? null : data;
    },
    async fetchRandomCommander(queryFilter) {
      return this.fetchRandom(this.combineFilters("is:commander", "-t:background", queryFilter));
    },
    async fetchRandomPartner(queryFilter) {
      return this.fetchRandom(this.combineFilters("o:/^partner$/", queryFilter));
    },
    async fetchRandomBackground(queryFilter) {
      return this.fetchRandom(this.combineFilters("t:background", queryFilter));
    },
    combineFilters() {
      if(arguments.length == 0)
        return '';
      const filters = [];
      for (let i = 0; i < arguments.length; i++) {
        filters.push(arguments[i]);
      }
      return filters.filter(f => f).join('%20');
    },
    getImageUrl(commander) {
      if (commander.image_uris?.normal) 
        return commander.image_uris.normal;

      if (commander.card_faces?.length > 1) 
        return commander.card_faces[commander.isTransformed ? 1 : 0].image_uris.normal;
      
      if(commander.card_faces?.length == 1)      
        return commander.card_faces[0].image_uris.normal;

      return 'https://via.placeholder.com/400x600';
    },

    async generateImage() {
      const canvas = this.$refs.canvas;
      const ctx = canvas.getContext("2d");

      const imagesPerRow = 3;
      const commanderCount = this.commanders.length;
      const rows = Math.ceil(commanderCount / imagesPerRow); 
      const width = 400;
      const height = 600;
      canvas.width = imagesPerRow * width; 
      canvas.height = rows * height;         
      
      const images = document.querySelectorAll('.commander-img');
      let count = 0;
      for (const img of images) {
        await new Promise(resolve => {
          if (img.complete) resolve();
          else img.onload = resolve;
        });        
        const x = (count % imagesPerRow) * width;
        const y = Math.floor(count / imagesPerRow) * height;
        ctx.drawImage(img, x, y, width, height);
        count++;
      }
      this.downloadImage(canvas);
    },

    downloadImage(canvas) {
      const link = document.createElement('a');
      link.href = canvas.toDataURL(); 
      link.download = 'commanders_image.png'; 
      link.click(); 
    },

  },
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>

<style >
.multiselect__tag {
    color: #2a2a2a;
    background: #ccc;
}

.multiselect__tag-icon::after {
    color: #2a2a2a;
}
</style>

<style scoped>
.loading {
  text-align: center;
  font-size: 1.5em;
  color: #007BFF;
}

.settings {
  border: 1px solid  #333;
  border-radius: 8px;
  margin: 20px auto;
